<template>
  <v-container fluid class="mt-6">
      <v-card
    class="mx-auto my-12"
    max-width="500"
  >

      
      <v-row>
        
       <v-card-title>
          <!-- 상단에 카드 이미지 표시, 파일경로는 assets 기준 -->
          <v-row align="center">          
            <!-- 우측에 안내글 표시 -->
            <v-col >
              <v-card-title primary-title>
                <div class="body-1"> 
                qr scan screen
                </div>
              </v-card-title>
            </v-col>
          </v-row>

      
          </v-card-title>

          <v-card-text>
          <v-row align="center" >
            
             <div class="d-inline ml-2">
               
                <v-btn block large @click="turnOn = true" color="orange" dark>qr scan</v-btn>
                  
              </div>
              <div class="d-inline ml-2">
                
                 <v-btn block large @click="turnOn = false" color="orange" dark>
                scan off
              </v-btn>
              </div>
            
          </v-row>
          

          
          <v-row align="center" >
          <h2> Input Value: {{ text || "Nothing" }}</h2>
          <StreamBarcodeReader v-if="turnOn"
              @decode="onDecode"
              @loaded="onLoaded"
          ></StreamBarcodeReader>
          </v-row>
          </v-card-text>
       
      </v-row>
      </v-card>

  </v-container>
</template>


<script>
  import { StreamBarcodeReader } from "vue-barcode-reader";
 
  
  export default {

    components: {
      StreamBarcodeReader,
    },

    data() {
      return {
        text: "",
        id: null,
        turnOn: false,
      }
    },

    
    methods: {

      onDecode(a, b, c) {
        console.log(a, b, c);
        this.text = a;
        if (this.id) clearTimeout(this.id);
        this.id = setTimeout(() => {
          if (this.text === a) {
            this.text = "";
          }
        }, 5000);
    },
    onLoaded() {
      console.log("load");
    },
      
    reload() {
      this.turnOn = true;
    },

    

  
  }
  }

</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}

.validation-success,
.validation-failure,
.validation-pending {
  position: absolute;
  width: 100%;
  height: 100%;

  background-color: rgba(255, 255, 255, .8);
  text-align: center;
  font-weight: bold;
  font-size: 1.4rem;
  padding: 10px;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
.validation-success {
  color: green;
}
.validation-failure {
  color: red;
}
</style>